.quizBoardContainer {
  background-color: lightblue;
  height: calc(100vh - 9em); /* Exclude space for the title */
  padding: 20px 0;
  border: 2px solid black;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.board {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
  width: 98%;
}

.question {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f0f0f0;
  width: 100%;
  height: 18vh;
  border: 1px solid black;
  cursor: pointer;
  transition: background-color 0.5s ease-out;
  padding: 5px;
  overflow: auto; /* This will add scroll bars as needed (horizontally and/or vertically) */
  -webkit-user-select: none; /* Safari */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently supported by Chrome, Opera, and Safari, and Firefox 69+ */
}

.question:not(:has(.top-left)) {
  justify-content: center;
  text-align: center;
}

.blue {
  background-color: blue;
}

.red {
  background-color: red;
}

.grey {
  background-color: darkgray;
}

.qtext {
  font-size: 1.3em;
  width: 100%;
  white-space: pre-wrap;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.qtext.top-left {
  align-items: left !important;
}

.katex span {
  display: inline-block !important; /* or inline, depending on the default for KaTeX */
}
